.pricing {
  width: 100%;
  padding: 6rem 1rem;
  background: #000;
}

.card-container {
  max-width: 1140px;
  margin: auto;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 40px;
}

.card {
  bottom: 1px solid #eee;
  color: #eee;
  text-align: center;
  padding: 1rem;
}

.card:hover {
  background-color: #c566df;
  transition: all 0.6s ease-in-out;
  color: #000;
}

.card h3 {
  font-size: 1.4rem;
  padding: 1rem;
}

.card h3:hover {
  color: #000;
  transition: all 0.6s ease-in-out;
}

.card p {
  padding: 10px 0;
}

.card p:hover {
  color: #000;
  transition: all 0.6s ease-in-out;
}

.bar {
  border-bottom: 1px solid #eee;
  width: 10%;
  margin: 1.5rem auto;
  display: block;
}

.btc {
  font-size: 1rem;
  font-weight: 600;
  padding: 1rem 0;
}

.card .btn {
  display: block;
  width: 80%;
  margin: 2rem auto;
}

@media screen and (max-width: 740px) {
  .card-container {
    max-width: 90%;
    margin: auto;
    grid-template-columns: 1fr;
  }
}
